/* paper flip effect */
.flipped .front,
.flipped .back {
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg);
}

.flipBack .front, 
.flipBack .back {
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
    -webkit-transform: rotateY(180);
            transform: rotateY(180);
}

.Book {
    height: 60vh;
    padding-top: 5%;
    
    height: 0;
    width: calc(((2 * (500px / 1.414)) + (48px * 2))*0.75);
    display: flex;
    justify-content: center;
    align-items: center;
    outline: 1000px solid rgba(0, 0, 0, 0.5);
    background-color: rgba(0, 0, 0, 0.5);
    margin: 0 auto;
    margin-top: 400px;
    position: absolute;
}


.book {
    position: relative;
    height: 70vh;
    width: 700px;
    margin: 10px;
    top: -250px;
}

.flipped {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;

}

@-webkit-keyframes shift {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }

    100% {
        -webkit-transform: translateX(210px);
                transform: translateX(210px);
    }
}

@keyframes shift {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }

    100% {
        -webkit-transform: translateX(210px);
                transform: translateX(210px);
    }
}

.shiftRight {
    -webkit-animation-name: shift;
            animation-name: shift;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.front,
.back {
    background-color: white;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    -webkit-transform-origin: left;
            transform-origin: left;
    transition: -webkit-transform 0.5;
    transition: transform 0.5;
    transition: transform 0.5, -webkit-transform 0.5;
    border-radius: 5px;
}

.back img {
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
}

.front {
    z-index: 1;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

.panel {
    width: 100%;
    height: 100%;
}

.hidden {
    display: none;
}
